import { fullPdfList } from "../config";


export default class PathService {
    //      
    //          ,  pdf-pdf
    static validation(path) {
        for (let index = 0; index < fullPdfList.length; index++) {
            //  1 , ..     /docx-pdf
            if (path === fullPdfList[index].link.substring(1)) {
                return true;
            }
        }
        return false;
    }
}